import React from 'react'
import { Container, Row, Col, Form, InputGroup, InputGroupAddon, Input, InputGroupText } from 'reactstrap'

const Benefits = () => (
    <section className="gym benefit " id="plan">
        <Container>
            <h3 className="oftHeading text-dark">Membership Benefits</h3>
            <Row>
                <Col lg="4" md="4" className="offset-md-2 offset-lg-0">
                    <div>
                        <center>
                            <img src="/assets/images/OTF/icons/heatl1.png" alt="img" />
                        </center>
                    </div>
                    <p className='p-light oftsubHeading'>
                        Easy online booking
                    </p>
                </Col>
                <Col lg="4" md="4" className="offset-md-2 offset-lg-0">
                    <div>
                        <center>
                            <img src="/assets/images/OTF/icons/heatl1.png" alt="img" />
                        </center>
                    </div>
                    <p className='p-light oftsubHeading'>
                        Easy online booking
                    </p>

                </Col>
                <Col lg="4" md="4" className="offset-md-2 offset-lg-0"
                >
                    <div class="locationImg">
                        <img src="/assets/images/OTF/membership/locationImg.png" alt="Avatar" className="image img-fluid" />
                        <div className="overlaylocation">
                            <div className="locationtext">
                                <h3 className='headingL mb-3'>Our locations</h3>
                                <Form>
                                    <InputGroup>
                                        <InputGroupAddon addonType="prepend"
                                        >
                                            <InputGroupText
                                                style={{
                                                    background: 'transparent', border: "0",
                                                    borderBottom: "1px solid white",
                                                    borderRadius: "0"
                                                }}
                                            >
                                                <i className="fa fa-search" aria-hidden="true"
                                                    style={{ color: 'white', fontSize: 18 }}>
                                                </i>
                                            </InputGroupText>
                                        </InputGroupAddon>
                                        <Input
                                            style={{
                                                background: 'transparent', color: 'white', border: "0",
                                                borderBottom: "1px solid white",
                                                borderRadius: "0",
                                                padding: "0"
                                            }}
                                            placeholder="Search for your nearest establishment">
                                        </Input>
                                    </InputGroup>
                                </Form>
                                <p className='subheadingL1 mt-3'>Book Class in Mercato Mall</p>
                                <p className='mb-3'>
                                    <a href="tel:+9714 340 1040" className='callL1'>
                                        <i className="fa fa-phone" aria-hidden="true">
                                        </i> Call Now
                                    </a>
                                </p>
                                <p className='subheadingL1'>Book Class in Times Square Center</p>
                                <p>
                                    <a href="tel:+9714 340 1040" className='callL1'>
                                        <i className="fa fa-phone" aria-hidden="true">
                                        </i> Call Now
                                    </a>
                                </p>
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>
        </Container>
    </section>
)


export default Benefits;